import React, { useState } from "react";
import type { RootState } from '../../store/store'
import { Button, Col, message, Row, Image, Space, Typography, Table, Popconfirm } from "antd";
import type { ColumnsType } from 'antd/es/table';
import { useTranslation } from 'react-i18next';
import OrdersModal from "./_components/Modal";
import { ProductDTO } from "types";
import { useOrderDeleteMutation, useOrderQuery, useCategoryListQuery } from "store/endpoints";
import { useSelector, useDispatch } from 'react-redux';
import { updateProductStates } from "../../store/slices/productSlice";

const Orders: React.FC = () => {
    const { t } = useTranslation()
    const { openModal, modalType } = useSelector((state: RootState) => state.productSlice)
    const dispatch = useDispatch()
    const [query, setQuery] = useState({ page: 1, pageSize: 20 })

    const categoryQuery = useCategoryListQuery();
    const orderQuery = useOrderQuery(query);
    const [orderDelete, { isLoading }] = useOrderDeleteMutation();
    const [updateData, setUpdateData] = useState();
    const handleUpdate = (data: any) => {
        setUpdateData(data);
        dispatch(updateProductStates({ openModal: true, modalType: 'update' }))
    };
    const handleDelete = (id: number | undefined) => {
        const category = orderDelete({ id }).unwrap();
        category
            .then((res) => {
                if (res.statusCode === 200) {
                    message.success("Muvaffaqiyati o'chirildi.");
                }
            })
            .catch((err) => {
                message.error(`Xatolik yuz berdi. Xatolik: ${err.message}`);
            });
    };

    const columns: ColumnsType<any> = [
        {
            title: "â„–",
            dataIndex: "id",
            key: "id",
            width: "5%",
            render: (item, record, index) => <span>{index + 1}</span>,
        },
        {
            title: t("productsMenu.name"),
            dataIndex: "name",
            key: "name",
            // width: "75%",
            render: (item, record: any) => {
                return <Space size={5}>
                    <Image
                        width={30}
                        height={30}
                        src={`http://147.182.130.242:3000/${record.image}`}
                        fallback="data:image/png;base64,"
                    />{item}</Space>
            }
        },
        {
            title: t("productsMenu.description"),
            dataIndex: "description",
            key: "description",
            // width: "75%",
            render: (item, record) => <>{item}</>
        },
        {
            title: t("productsMenu.price"),
            dataIndex: "price",
            key: "price",
            // width: "75%",
            render: (item, record) => <>{item}</>
        },
        {
            title: t("productsMenu.discount"),
            dataIndex: "discount",
            key: "discount",
            // width: "75%",
            render: (item, record) => <>{item}</>
        },
        {
            title: t("actions"),
            key: "action",
            width: "20%",
            render: (item: ProductDTO, record, index) => {
                return (
                    <Row wrap={false} gutter={5}>
                        <Col>
                            <Button
                                size="small"
                                type="primary"
                                ghost
                                onClick={() => handleUpdate(item)}
                            >
                                {t('edit')}
                            </Button>
                        </Col>
                        <Col>
                            <Popconfirm
                                okText={t("yes")}
                                cancelText={t("no")}
                                onConfirm={() => handleDelete(item.id)}
                                title={t("sureDelete")}
                            >
                                <Button size="small" danger disabled={isLoading}>
                                    {t("delete")}
                                </Button>
                            </Popconfirm>
                        </Col>
                    </Row>
                );
            },
        },
    ];
    const handleCloseModal = () => {
        dispatch(updateProductStates({ openModal: false, modalType: '' }))
        setUpdateData(undefined)
    }

    const pagination =
    {
        total: orderQuery.data?.meta?.total * orderQuery.data?.meta?.pagesize,
        page: query.page,
        pageSizeOptions: ["20", "50", "100"],
        showQuickJumper: true, showSizeChanger: true,
        pageSize: query.pageSize,
        current: query.page,
        onChange: (page: number, pageSize: number) => {
            setQuery({ page: page, pageSize: pageSize })
        }
    }

    const modalProps = {
        title: modalType === 'update' ? t('edit') : t('add'),
        open: openModal,
        okText: modalType === 'update' ? t('edit') : t('add'),
        cancelText: t('close'),
        onCancel: handleCloseModal
    }

    return (
        <>
            <Row>
                <Col span={20}>
                    <Typography.Title level={2}>{t('menus.orders')}</Typography.Title>
                </Col>
                <Col span={4}>
                    <Button
                        type="primary"
                        onClick={() => dispatch(updateProductStates({ openModal: true, modalType: 'create' }))}
                    >
                        {t('add')}
                    </Button>
                </Col>
            </Row>

            <Table
                columns={columns}
                dataSource={orderQuery.data?.data}
                loading={orderQuery.isFetching}
                rowKey={record => record.id}
                pagination={pagination}
            />
            <OrdersModal updateData={updateData} t={t} modalType={modalType} categoryData={categoryQuery.data?.data} {...modalProps} />
        </>
    );
};

export default Orders;
